video {
  height: 100%;
  width: 100%;
  position: relative;
  object-fit: cover;
  border-radius: 5px;
  float: left;
}

.streamcomponent div {
  position: absolute;
  padding: 2px 8px;
  font-size: 15px;
  color: white;
  font-weight: 300;
  border-bottom-right-radius: 4px;
}
p {
  margin: 0;
}
